import NextAuth from 'next-auth';
import GoogleProvider from 'next-auth/providers/google';
import jwt from 'jsonwebtoken'; // Import jsonwebtoken

export default NextAuth({
  providers: [
    GoogleProvider({
      clientId: process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID,
      clientSecret: process.env.NEXT_PUBLIC_GOOGLE_CLIENT_SECRET,
    }),
  ],
  secret: process.env.NEXTAUTH_SECRET, // Ensure this matches your .env file
  session: {
    strategy: 'jwt', // Use JWT-based sessions
  },
  callbacks: {
    async jwt({ token, account }) {
      if (account) {
        // Add custom claims to the token
        const customClaims = {
          accessToken: account.access_token,
          userId: account.providerAccountId, // Add any other custom data here
        };

        // Sign the token with jsonwebtoken
        const signedToken = jwt.sign(
          { ...token, ...customClaims }, // Combine existing token data with custom claims
          process.env.NEXTAUTH_SECRET, // Use the same secret for signing
          { expiresIn: '1d' } // Set token expiration (e.g., 1 day)
        );

        return signedToken; // Return the signed token
      }

      // Decode the token if it already exists
      if (typeof token === 'string') {
        try {
          const decodedToken = jwt.verify(token, process.env.NEXTAUTH_SECRET);
          return decodedToken;
        } catch (err) {
          console.error('JWT verification failed:', err);
          throw new Error('Invalid token');
        }
      }

      return token;
    },
    async session({ session, token }) {
      // Attach the access token and other custom data to the session
      if (typeof token === 'string') {
        const decodedToken = jwt.verify(token, process.env.NEXTAUTH_SECRET);
        session.user.accessToken = decodedToken.accessToken;
        session.user.userId = decodedToken.userId; // Attach custom claims
      } else {
        session.user.accessToken = token.accessToken;
        session.user.userId = token.userId; // Attach custom claims
      }

      return session;
    },
  },
});